import React, { useState, Fragment, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  fetchBrandsAsync,
  fetchCategoriesAsync,
  fetchProductsByFiltersAsync,
  selectAllProducts,
  selectCategories,
  selectProductListStatus,
  selectTotalItems,
} from '../productSlice';
import { Dialog, Disclosure, Menu, Transition } from '@headlessui/react';
import {
  XMarkIcon,
  ChevronLeftIcon,
  ChevronRightIcon,
  StarIcon,
  ChevronDownIcon,
  FunnelIcon,
  MinusIcon,
  PlusIcon,
  Squares2X2Icon,
} from '@heroicons/react/20/solid';
import { Link } from 'react-router-dom';
import { ITEMS_PER_PAGE } from '../../../app/constants';
import Pagination from '../../common/Pagination';

const sortOptions = [
  { name: 'Best Rating', sort: 'rating', order: 'desc', current: false },
  { name: 'Price: Low to High', sort: 'discountPrice', order: 'asc', current: false },
  { name: 'Price: High to Low', sort: 'discountPrice', order: 'desc', current: false },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

export default function ProductList() {
  const dispatch = useDispatch();
  const products = useSelector(selectAllProducts);
  const categories = useSelector(selectCategories);
  const totalItems = useSelector(selectTotalItems);
  const status = useSelector(selectProductListStatus);

  const filters = [
    {
      id: 'category',
      name: 'Category',
      options: categories,
    },
  ];

  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState({});
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false);
  const [page, setPage] = useState(1);

  const handleFilter = (e, section, option) => {
    const newFilter = { ...filter };
    if (e.target.checked) {
      newFilter[section.id] = newFilter[section.id] ? [...newFilter[section.id], option.value] : [option.value];
    } else {
      newFilter[section.id] = newFilter[section.id].filter((el) => el !== option.value);
    }
    setFilter(newFilter);
  };

  const handleSort = (option) => {
    const sort = { _sort: option.sort, _order: option.order };
    setSort(sort);
  };

  const handlePage = (page) => {
    setPage(page);
  };

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchProductsByFiltersAsync({ filter, sort, pagination }));
  }, [dispatch, filter, sort, page]);

  useEffect(() => {
    setPage(1);
  }, [totalItems, sort]);

  useEffect(() => {
    dispatch(fetchBrandsAsync());
    dispatch(fetchCategoriesAsync());
  }, [dispatch]);

  return (
    <div className="bg-base-100 min-h-screen">
      {/* Mobile Filters */}
      <MobileFilters
        mobileFiltersOpen={mobileFiltersOpen}
        setMobileFiltersOpen={setMobileFiltersOpen}
        handleFilter={handleFilter}
        filters={filters}
      />

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <div className="flex items-center justify-between border-b border-base-300 pb-6">
          <h1 className="text-3xl font-bold text-base-content tracking-tight">All Products</h1>
          <div className="flex items-center space-x-4">
            {/* Sort Dropdown */}
            <Menu as="div" className="relative z-10">
              <Menu.Button className="btn btn-ghost btn-sm flex items-center">
                Sort
                <ChevronDownIcon className="ml-1 h-5 w-5" />
              </Menu.Button>
              <Transition
                as={Fragment}
                enter="transition ease-out duration-150"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-100"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
              >
                <Menu.Items className="dropdown-content menu p-2 shadow bg-base-100 rounded-box w-52 absolute right-0 mt-2">
                  {sortOptions.map((option) => (
                    <Menu.Item key={option.name}>
                      {({ active }) => (
                        <button
                          onClick={() => handleSort(option)}
                          className={classNames(
                            active ? 'bg-base-200' : '',
                            'block w-full text-left p-2 rounded text-base-content hover:bg-base-200'
                          )}
                        >
                          {option.name}
                        </button>
                      )}
                    </Menu.Item>
                  ))}
                </Menu.Items>
              </Transition>
            </Menu>

            {/* Grid View Toggle */}
            <button className="btn btn-ghost btn-sm">
              <Squares2X2Icon className="h-5 w-5" />
            </button>

            {/* Mobile Filter Toggle */}
            <button
              className="btn btn-ghost btn-sm lg:hidden"
              onClick={() => setMobileFiltersOpen(true)}
            >
              <FunnelIcon className="h-5 w-5" />
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="mt-8 grid grid-cols-1 gap-8 lg:grid-cols-4">
          {/* Desktop Filters */}
          <div className="hidden lg:block">
            <DesktopFilters handleFilter={handleFilter} filters={filters} />
          </div>

          {/* Product Grid */}
          <div className="lg:col-span-3">
            <ProductGrid products={products} status={status} />
          </div>
        </div>

        {/* Pagination */}
        <div className="mt-12">
          <Pagination page={page} setPage={setPage} handlePage={handlePage} totalItems={totalItems} />
        </div>
      </main>
    </div>
  );
}

// Mobile Filters Component
function MobileFilters({ mobileFiltersOpen, setMobileFiltersOpen, handleFilter, filters }) {
  return (
    <Transition.Root show={mobileFiltersOpen} as={Fragment}>
      <Dialog as="div" className="relative lg:hidden" onClose={setMobileFiltersOpen}>
        <Transition.Child
          as={Fragment}
          enter="transition-opacity ease-linear duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="transition-opacity ease-linear duration-300"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black bg-opacity-30" />
        </Transition.Child>

        <div className="fixed inset-0 flex justify-end">
          <Transition.Child
            as={Fragment}
            enter="transition ease-in-out duration-300 transform"
            enterFrom="translate-x-full"
            enterTo="translate-x-0"
            leave="transition ease-in-out duration-300 transform"
            leaveFrom="translate-x-0"
            leaveTo="translate-x-full"
          >
            <Dialog.Panel className="relative w-full max-w-xs bg-base-100 shadow-xl p-6">
              <div className="flex justify-between items-center">
                <h2 className="text-lg font-semibold text-base-content">Filters</h2>
                <button
                  onClick={() => setMobileFiltersOpen(false)}
                  className="btn btn-ghost btn-sm"
                >
                  <XMarkIcon className="h-6 w-6" />
                </button>
              </div>

              <form className="mt-6 space-y-6">
                {filters.map((section) => (
                  <Disclosure key={section.id} as="div">
                    {({ open }) => (
                      <>
                        <Disclosure.Button className="flex justify-between w-full py-3 text-sm font-medium text-base-content">
                          <span>{section.name}</span>
                          <span>{open ? <MinusIcon className="h-5 w-5" /> : <PlusIcon className="h-5 w-5" />}</span>
                        </Disclosure.Button>
                        <Transition
                          enter="transition ease-out duration-200"
                          enterFrom="opacity-0"
                          enterTo="opacity-100"
                          leave="transition ease-in duration-150"
                          leaveFrom="opacity-100"
                          leaveTo="opacity-0"
                        >
                          <Disclosure.Panel className="mt-2 space-y-4">
                            {section.options.map((option, optionIdx) => (
                              <div key={option.value} className="form-control">
                                <label className="label cursor-pointer flex items-center">
                                  <input
                                    id={`mobile-filter-${section.id}-${optionIdx}`}
                                    name={`${section.id}[]`}
                                    value={option.value}
                                    type="checkbox"
                                    onChange={(e) => handleFilter(e, section, option)}
                                    className="checkbox checkbox-primary"
                                  />
                                  <span className="label-text ml-3 text-base-content">{option.label}</span>
                                </label>
                              </div>
                            ))}
                          </Disclosure.Panel>
                        </Transition>
                      </>
                    )}
                  </Disclosure>
                ))}
              </form>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition.Root>
  );
}

// Desktop Filters Component
function DesktopFilters({ handleFilter, filters }) {
  return (
    <form className="space-y-6">
      {filters.map((section) => (
        <Disclosure key={section.id} as="div" className="card bg-base-100 shadow-sm p-4">
          {({ open }) => (
            <>
              <Disclosure.Button className="flex justify-between w-full text-sm font-medium text-base-content">
                <span>{section.name}</span>
                <span>{open ? <MinusIcon className="h-5 w-5" /> : <PlusIcon className="h-5 w-5" />}</span>
              </Disclosure.Button>
              <Transition
                enter="transition ease-out duration-200"
                enterFrom="opacity-0"
                enterTo="opacity-100"
                leave="transition ease-in duration-150"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
              >
                <Disclosure.Panel className="mt-4 space-y-3">
                  {section.options.map((option, optionIdx) => (
                    <div key={option.value} className="form-control">
                      <label className="label cursor-pointer flex items-center">
                        <input
                          id={`filter-${section.id}-${optionIdx}`}
                          name={`${section.id}[]`}
                          value={option.value}
                          type="checkbox"
                          onChange={(e) => handleFilter(e, section, option)}
                          className="checkbox checkbox-primary"
                        />
                        <span className="label-text ml-3 text-base-content">{option.label}</span>
                      </label>
                    </div>
                  ))}
                </Disclosure.Panel>
              </Transition>
            </>
          )}
        </Disclosure>
      ))}
    </form>
  );
}

// Product Grid Component
function ProductGrid({ products, status }) {
  return (
    <div className="bg-base-100">
      <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {status === 'loading' ? (
            <div className="card h-80 w-full flex justify-center items-center bg-base-200 rounded-box">
              <span className="loading loading-spinner loading-lg text-primary"></span>
            </div>
          ) : null}
          {products.map((product) => (
            <Link
              key={product.id}
              to={`/product-detail/${product.id}`}
              className="card bg-base-100 shadow-md hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1"
            >
              <figure className="relative">
                <img
                  src={product.thumbnail}
                  alt={product.title}
                  className="object-cover w-full h-48 transition-transform duration-300 group-hover:scale-105"
                />
              </figure>
              <div className="card-body p-4">
                <h2 className="card-title text-base font-medium text-base-content">{product.title}</h2>
                <div className="flex items-center text-sm text-base-content mb-2">
                  <StarIcon className="w-5 h-5 mr-1 text-yellow-500" />
                  <span>{product.rating}</span>
                </div>
                <div className="flex justify-between items-center">
                  <p className="text-lg font-semibold text-base-content">₹{product.discountPrice}</p>
                  {product.price !== product.discountPrice && (
                    <p className="text-sm line-through text-gray-600">₹{product.price}</p>
                  )}
                </div>
                {product.deleted && <p className="text-sm text-error mt-2">Product deleted</p>}
                {product.stock <= 0 && <p className="text-sm text-error mt-2">Out of stock</p>}
              </div>
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
}