import { Fragment } from 'react';
import { Menu, Transition } from '@headlessui/react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { selectItems } from '../cart/cartSlice';
import { selectUserInfo } from '../user/userSlice';
import ThemeSwitcher from './ThemeSwitcher'; // Adjust the path
function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

// Function to generate initials from user's name
const getInitials = (name) => {
  if (!name) return 'U'; // Default to 'U' if no name
  const names = name.split(' ');
  const initials = names.map(n => n.charAt(0).toUpperCase()).join('');
  return initials.slice(0, 2); // Limit to 2 characters
};

function NavBar({ children }) {
  const items = useSelector(selectItems);
  const userInfo = useSelector(selectUserInfo);

  const navigation = [
    { name: 'Home', link: '/', show: true }, // Always visible
    { name: 'Products', link: '/admin', show: userInfo && userInfo.role === 'admin' },
    { name: 'Orders', link: '/admin/orders', show: userInfo && userInfo.role === 'admin' },
    { name: 'Admin', link: '/admin', show: userInfo && userInfo.role === 'admin' },
    { name: 'My Profile', link: '/profile', show: userInfo },
    { name: 'My Orders', link: '/my-orders', show: userInfo },
    { name: 'Sign out', link: '/logout', show: userInfo },
    { name: 'Sign in', link: '/login', show: !userInfo }, // Visible when not logged in
  ];

  return (
    <div className="min-h-full">
      <div className="navbar bg-gray-800 text-gray-300">
        <div className="navbar-start">
          {/* Mobile Menu Button */}
          <div className="dropdown">
            <label tabIndex={0} className="btn btn-ghost lg:hidden">
              <svg
                className="h-6 w-6"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16m-7 6h7"
                />
              </svg>
            </label>
            <ul
              tabIndex={0}
              className="menu menu-sm dropdown-content mt-3 z-10 p-2 shadow bg-gray-700 text-gray-300 rounded-box w-52"
            >
              {navigation.map((item) =>
                item.show ? (
                  <li key={item.name}>
                    <Link to={item.link} className="hover:bg-gray-600">
                      {item.name}
                    </Link>
                  </li>
                ) : null
              )}
            </ul>
          </div>
          {/* Logo */}
          <Link to="/" className="btn btn-ghost normal-case text-xl">
            <img
              className="h-12 w-12"
              src="/mashcraft_logo_round.png"
              alt="MashCraft Logo"
            />
          </Link>
        </div>

        {/* Desktop Navigation */}
        <div className="navbar-center hidden lg:flex">
          <ul className="menu menu-horizontal px-1">
            {navigation.map((item) =>
              item.show ? (
                <li key={item.name}>
                  <Link
                    to={item.link}
                    className="hover:bg-gray-700 hover:text-white px-3 py-2 rounded-md"
                  >
                    {item.name}
                  </Link>
                </li>
              ) : null
            )}
          </ul>
        </div>

        {/* Right Side (Cart and User Menu) */}
        <div className="navbar-end">
          {/* Cart */}
          <Link to="/cart" className="btn btn-ghost relative" aria-label={`View cart with ${items.length} items`}>
            <svg
              className="h-6 w-6"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4 4h16l2 7h-6M9 20a2 2 0 110-4 2 2 0 010 4zM20 4H7l-1 6h15V4z"
              />
            </svg>
            {items.length > 0 && (
              <span className="badge badge-sm badge-error absolute top-0 right-0">
                {items.length}
              </span>
            )}
          </Link>
          <ThemeSwitcher />

          {/* User Menu */}
          {userInfo && (
            <Menu as="div" className="relative ml-3">
              <Menu.Button className="btn btn-ghost btn-circle">
                {userInfo.imageUrl ? (
                  <img
                    className="h-8 w-8 rounded-full object-cover"
                    src={userInfo.imageUrl}
                    alt={`${userInfo.name}'s profile`}
                    onError={(e) => {
                      e.target.style.display = 'none';
                      e.target.nextSibling.style.display = 'flex';
                    }}
                  />
                ) : null}
                <div
                  className={`h-8 w-8 rounded-full bg-primary flex items-center justify-center ${
                    userInfo.imageUrl ? 'hidden' : ''
                  }`}
                >
                  <span className="text-white text-sm font-medium">
                    {getInitials(userInfo?.name)}
                  </span>
                </div>
              </Menu.Button>
              <Transition
                as={Fragment}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
              >
                <Menu.Items className="absolute right-0 mt-2 w-48 origin-top-right bg-white rounded-md shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none z-10">
                  {navigation.map((item) =>
                    item.show ? (
                      <Menu.Item key={item.name}>
                        {({ active }) => (
                          <Link
                            to={item.link}
                            className={classNames(
                              active ? 'bg-gray-100' : '',
                              'block px-4 py-2 text-sm text-gray-700'
                            )}
                          >
                            {item.name}
                          </Link>
                        )}
                      </Menu.Item>
                    ) : null
                  )}
                </Menu.Items>
              </Transition>
            </Menu>
          )}
        </div>
      </div>
      <main>{children}</main>
    </div>
  );

}

export default NavBar;