import React, { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import { useSelector } from 'react-redux';
import { selectCurrentOrder } from "../features/order/orderSlice";
import CheckoutForm from "./CheckoutForm";
import "../Stripe.css";

// Load Stripe outside of component render
const stripePromise = loadStripe("pk_test_51OsWWxSHY3SybBtgkerMu36svNhsubCq6s2SDrrNFdZ1pVgiDOqrzeRzYIX7oHS71YdHCJysmohfAinX9DWiudRP00Ajcw09G6");

export default function StripeCheckout() {
  const [clientSecret, setClientSecret] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const currentOrder = useSelector(selectCurrentOrder);

  useEffect(() => {
    if (!currentOrder?.id || !currentOrder?.totalAmount) {
      setError("No order information available");
      setLoading(false);
      return;
    }

    const createPaymentIntent = async () => {
      try {
        setLoading(true);
        setError(null);

        const response = await fetch("/create-payment-intent", {
          method: "POST",
          headers: { 
            "Content-Type": "application/json",
            "Accept": "application/json"
          },
          body: JSON.stringify({ 
            totalAmount: currentOrder.totalAmount,
            orderId: currentOrder.id 
          }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        
        if (data.clientSecret) {
          setClientSecret(data.clientSecret);
        } else {
          throw new Error("No client secret received from server");
        }
      } catch (err) {
        setError(err.message || "Failed to initialize payment. Please try again.");
      } finally {
        setLoading(false);
      }
    };

    createPaymentIntent();
  }, [currentOrder?.id, currentOrder?.totalAmount]);
  const appearance = {
    theme: document.documentElement.getAttribute('data-theme') === 'cupcake' ? 'night' : 'stripe',
    variables: {
      colorPrimary: document.documentElement.getAttribute('data-theme') === 'cupcake' 
        ? 'oklch(82% 0.12 346.018)' 
        : '#4B46E2',
      colorBackground: document.documentElement.getAttribute('data-theme') === 'cupcake' 
        ? 'oklch(97% 0.014 343.198)' 
        : '#ffffff',
      colorText: document.documentElement.getAttribute('data-theme') === 'cupcake' 
        ? 'oklch(23.574% 0.066 313.189)' 
        : '#1F2937',
      colorDanger: document.documentElement.getAttribute('data-theme') === 'cupcake' 
        ? 'oklch(64% 0.246 16.439)' 
        : '#dc2626',
      borderRadius: document.documentElement.getAttribute('data-theme') === 'cupcake' 
        ? '2rem' 
        : '8px',
    },
    rules: {
      '.Input': {
        border: document.documentElement.getAttribute('data-theme') === 'cupcake' 
          ? '2px solid oklch(89% 0.061 343.231)' 
          : '1px solid #d1d5db',
      },
      '.Input:focus': {
        borderColor: document.documentElement.getAttribute('data-theme') === 'cupcake' 
          ? 'oklch(82% 0.12 346.018)' 
          : '#4B46E2',
      },
    },
  };
  const options = {
    clientSecret,
    appearance,
  };

  return (
<div className="min-h-screen bg-base-100 py-6">
      <div className="container mx-auto px-4">
        <div className="card bg-base-200 shadow-xl p-6 max-w-2xl mx-auto">
          <h2 className="text-2xl font-bold mb-6 text-center text-base-content">
            Complete Your Payment
          </h2>
          {loading && (
            <div className="flex justify-center items-center h-64">
              <span className="loading loading-spinner loading-lg text-primary"></span>
            </div>
          )}

          {error && (
            <div className="alert alert-error mb-6">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="stroke-current shrink-0 h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <span>{error}</span>
            </div>
          )}

          {!loading && !error && clientSecret && (
            <Elements options={options} stripe={stripePromise}>
              <CheckoutForm />
            </Elements>
          )}

          {!loading && !clientSecret && !error && (
            <div className="alert alert-warning">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="stroke-current shrink-0 h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                />
              </svg>
              <span>Payment initialization failed. Please refresh the page.</span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}