import { useState, useEffect } from 'react';

function ThemeSwitcher() {
  const [theme, setTheme] = useState('light');

  useEffect(() => {
    // Apply the theme to the HTML element
    document.documentElement.setAttribute('data-theme', theme);
    // Optional: Save to localStorage to persist theme choice
    localStorage.setItem('theme', theme);
  }, [theme]);

  useEffect(() => {
    // Load saved theme from localStorage on mount
    const savedTheme = localStorage.getItem('theme');
    if (savedTheme) {
      setTheme(savedTheme);
    }
  }, []);

  const toggleTheme = () => {
    setTheme(theme === 'light' ? 'cupcake' : 'light');
  };

  return (
    <div className="dropdown dropdown-end">
      <label tabIndex={0} className="btn btn-ghost btn-circle">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
          />
        </svg>
      </label>
      <ul
        tabIndex={0}
        className="dropdown-content menu p-2 shadow bg-base-100 rounded-box w-52 text-base-content"
      >
        <li>
          <button
            onClick={() => setTheme('light')}
            className={theme === 'light' ? 'active' : ''}
          >
            Light
          </button>
        </li>
        <li>
          <button
            onClick={() => setTheme('cupcake')}
            className={theme === 'cupcake' ? 'active' : ''}
          >
            Cupcake
          </button>
        </li>
      </ul>
    </div>
  );
}

export default ThemeSwitcher;