import { useState, useEffect } from 'react';
import { StarIcon } from '@heroicons/react/20/solid';
import { RadioGroup } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import {
  fetchProductByIdAsync,
  selectProductById,
  selectProductListStatus,
} from '../productSlice';
import { useParams } from 'react-router-dom';
import { addToCartAsync, selectItems } from '../../cart/cartSlice';
import { toast } from 'react-toastify';
import { Grid } from 'react-loader-spinner';

function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

export default function ProductDetail() {
  const [selectedColor, setSelectedColor] = useState();
  const [selectedSize, setSelectedSize] = useState();
  const items = useSelector(selectItems);
  const product = useSelector(selectProductById);
  const dispatch = useDispatch();
  const params = useParams();
  const status = useSelector(selectProductListStatus);

  const handleCart = (e) => {
    e.preventDefault();
    if (items.findIndex((item) => item.product.id === product.id) < 0) {
      const newItem = {
        product: product.id,
        quantity: 1,
        ...(selectedColor && { color: selectedColor }),
        ...(selectedSize && { size: selectedSize }),
      };
      dispatch(addToCartAsync(newItem));
    } else {
      toast.error('Item Already Added');
    }
  };

  useEffect(() => {
    dispatch(fetchProductByIdAsync(params.id));
  }, [dispatch, params.id]);

  return (
    <div className="bg-base-100 min-h-screen">
      {status === 'loading' && (
        <div className="flex justify-center items-center h-64">
          <Grid
            height="80"
            width="80"
            color="#4B46E2"
            ariaLabel="grid-loading"
            radius="12.5"
            wrapperStyle={{}}
            wrapperClass=""
            visible={true}
          />
        </div>
      )}

      {product && (
        <div className="container mx-auto px-4 py-6 md:py-8">
          {/* Back to Home and Breadcrumb */}
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6 gap-4">
            <nav className="breadcrumb">
              <ol className="flex items-center gap-2 flex-wrap">
                {product.breadcrumbs?.map((breadcrumb) => (
                  <li key={breadcrumb.id}>
                    <a href={breadcrumb.href} className="link link-hover text-gray-600">
                      {breadcrumb.name}
                    </a>
                  </li>
                   ))}
                <li className="bg-pink-200 text-gray-600 px-1.5 py-0.5 rounded-md text-lg-b">{product.title}</li>              
              </ol>
            </nav> 
            <Link to="/" className="btn btn-outline btn-sm md:btn-md w-40">
              Back to Home
            </Link>
          </div>

          {/* Main Content */}
          <div className="grid lg:grid-cols-2 gap-6 md:gap-8">
            {/* Image Gallery */}
            <div className="carousel carousel-center rounded-box shadow-lg w-full">
              {product.images?.map((image, index) => (
                <div key={index} className="carousel-item w-full">
                  <img
                    src={image}
                    alt={`${product.title} - ${index + 1}`}
                    className="w-full h-[300px] md:h-[500px] object-cover"
                  />
                </div>
              ))}
            </div>

            {/* Product Info */}
            <div className="card bg-white shadow-xl p-4 md:p-6">
              <h1 className="text-2xl md:text-3xl font-bold mb-4">{product.title}</h1>

              {/* Pricing */}
              <div className="flex items-center gap-4 mb-6">
              <p className="bg-red-600 text-white font-bold px-1.5 py-0.5 rounded-md text-lg-b">Hurry very few Left</p> 
                <p className="text-lg md:text-xl text-red-500 line-through">₹{product.price}</p>
                <p className="text-2xl md:text-3xl font-semibold text-black">
                ₹{product.discountPrice}
                </p>
              </div>

              {/* Rating */}
              <div className="flex items-center mb-6">
                {[0, 1, 2, 3, 4].map((rating) => (
                  <StarIcon
                    key={rating}
                    className={classNames(
                      product.rating > rating ? 'text-yellow-400' : 'text-gray-200',
                      'h-5 w-5 flex-shrink-0'
                    )}
                  />
                ))}
                <span className="ml-2 text-sm text-gray-600">
                  ({product.rating} / 5)
                </span>
              </div>

              {/* Form Options */}
              <form onSubmit={handleCart}>
                {/* Colors */}
                {product.colors?.length > 0 && (
                  <div className="mb-6">
                    <h3 className="text-base md:text-lg font-medium mb-2">Color</h3>
                    <RadioGroup
                      value={selectedColor}
                      onChange={setSelectedColor}
                      className="flex flex-wrap gap-3"
                    >
                      {product.colors.map((color) => (
                        <RadioGroup.Option
                          key={color.name}
                          value={color}
                          className={({ checked }) =>
                            classNames(
                              checked ? 'ring-2 ring-primary' : '',
                              'cursor-pointer rounded-full p-1'
                            )
                          }
                        >
                          <span
                            className={classNames(
                              color.class,
                              'block h-8 w-8 rounded-full border border-gray-300'
                            )}
                            title={color.name}
                          />
                        </RadioGroup.Option>
                      ))}
                    </RadioGroup>
                  </div>
                )}

                {/* Sizes */}
                {product.sizes?.length > 0 && (
                  <div className="mb-6">
                    <div className="flex justify-between items-center mb-2">
                      <h3 className="text-base md:text-lg font-medium">Size</h3>
                      <a href="#" className="link link-primary text-sm">
                        Size guide
                      </a>
                    </div>
                    <RadioGroup
                      value={selectedSize}
                      onChange={setSelectedSize}
                      className="grid grid-cols-4 md:grid-cols-5 gap-2"
                    >
                      {product.sizes.map((size) => (
                        <RadioGroup.Option
                          key={size.name}
                          value={size}
                          disabled={!size.inStock}
                          className={({ checked }) =>
                            classNames(
                              size.inStock
                                ? 'btn btn-outline btn-sm h-10'
                                : 'btn btn-outline btn-sm h-10 btn-disabled',
                              checked ? 'btn-primary text-white' : '',
                              'text-sm min-w-[50px]'
                            )
                          }
                        >
                          {size.name}
                        </RadioGroup.Option>
                      ))}
                    </RadioGroup>
                  </div>
                )}

                {/* Add to Cart Button */}
                <button
                  type="submit"
                  className="btn btn-primary w-full md:w-2/3 mx-auto block text-white btn-md h-12"
                >
                  Add to Cart
                </button>
              </form>
            </div>
          </div>

          {/* Description and Details */}
          <div className="mt-6 md:mt-8 grid lg:grid-cols-2 gap-6 md:gap-8">
            <div className="card bg-white shadow-xl p-4 md:p-6">
              <h2 className="text-lg md:text-xl font-bold mb-4">Description</h2>
              <p className="text-gray-600 text-sm md:text-base">{product.description}</p>
            </div>

            {product.highlights && (
              <div className="card bg-white shadow-xl p-4 md:p-6">
                <h2 className="text-lg md:text-xl font-bold mb-4">Highlights</h2>
                <ul className="list-disc pl-5 space-y-2 text-sm md:text-base">
                  {product.highlights.map((highlight) => (
                    <li key={highlight} className="text-gray-600">
                      {highlight}
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}